.form {
    width: 280px;
    padding-top: 40px;
    padding-left: 60px;

}

.label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}
.input, 
.button {
    font-size: 18px;
    line-height: 1.8;
    border: 1px solid gray;
    border-radius: 5px;
    padding: 0 15px;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
            0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

